import React, { useEffect, useState } from 'react'
import Session from './Login'
import io from 'socket.io-client'
import audio from "../audio/audio.mp3"


const socket = io('https://chat-app-serverside.herokuapp.com/')
function Home(_user) {
    let _audio = new Audio(audio);
    let _name = _user.user.displayName;
    function formatAMPM(date) {
        var hours = date.getHours();
        var minutes = date.getMinutes();
        var ampm = hours >= 12 ? 'pm' : 'am';
        hours = hours % 12;
        hours = hours ? hours : 12; // the hour '0' should be '12'
        minutes = minutes < 10 ? '0' + minutes : minutes;
        var strTime = hours + ':' + minutes + ' ' + ampm;
        return strTime;
    }
    const appendJoin = (message) => {
        let chatBox = document.getElementById('chat-box');
        const messageElement = document.createElement('div');
        messageElement.className = 'sender-bubble-wrapper';
        messageElement.innerHTML = `<div class="sender message-bubble"><div style="font-style:italic;" class="bubble-text">` + message + `</div><div class="bubble-time">` + formatAMPM(new Date()) + `</div></div></div>`
        chatBox.append(messageElement)
    }
    const sentMessage = (message) => {
        let chatBox = document.getElementById('chat-box');
        const messageElement = document.createElement('div');
        messageElement.className = 'sender-bubble-wrapper';
        messageElement.innerHTML = `<div class="sender message-bubble"><div class="bubble-name">You</div><div class="bubble-text">` + message + `</div><div class="bubble-time">` + formatAMPM(new Date()) + `</div></div>`
        chatBox.append(messageElement)
    }
    const [mute, setMute] = useState(false);
    
    const recieveMessage = (name, message) => {
            _audio.play();
        let chatBox = document.getElementById('chat-box');
        const messageElement = document.createElement('div');
        messageElement.className = 'reciever-bubble-wrapper';
        messageElement.innerHTML = `<div class="message-bubble">
        <div class="bubble-name">`+ name + `</div>
        <div class="bubble-text">`+ message + `</div>
        <div class="bubble-time">`+ formatAMPM(new Date())
            + `</div>
        </div>`
        chatBox.append(messageElement)
    }
    const enterKey = (e) => {
        if (e.key === "Enter")
            sendMessage();
    }
    const [message, setMessage] = useState('');
    const sendMessage = () => {
        let data = {
            message: message,
            name: _name
        }
        socket.emit('send-message', (data));
        sentMessage(message)
        setMessage("");
        document.getElementById('message').blur()
        scrollDown();
    }
    const scrollDown = () => {
        var objDiv = document.getElementById("chat-box");
        objDiv.scrollTop = objDiv.scrollHeight;
        document.getElementById("chat-box").addEventListener('scroll', () => {
            if (document.getElementById("chat-box").scrollTop / document.getElementById("chat-box").scrollHeight > 0.50) {
                document.getElementById("scrollDown").style.display = 'none';
            }
            else {
                document.getElementById("scrollDown").style.display = 'block';
            }
        })
    }
    useEffect(() => {
        socket.emit('new-user-joined', _name)

        socket.on('user-joined', (name) => {
            appendJoin(name + ' Joined the Chat.')
        })
    }, [])
    useEffect(() => {

        socket.on('recieve-message', (data) => {
            recieveMessage(data.name, data.message)
        });

    }, [])
    return (
        <div className="home-container">
            <div className="main-chat-container">
                <div className="chat-header">
                    <div className="m-10">
                        <div className="header-text">Realtime Chat Around the World</div>
                    </div>
                    <div className="m-10">
                        <div onClick={() => setMute(!mute)}>{(mute) ? (<i class="fas fa-volume-mute"></i>) : (<i class="fas fa-volume-up"></i>)}</div>
                        <Session.SignOut />
                    </div>
                </div>
                <div id="chat-box" className="chat-box">

                </div>
                <button onClick={(() => scrollDown())} id="scrollDown"><i className="fas fa-arrow-alt-circle-down"></i></button>
                <div id="send-message-wrapper" className="send-message-wrapper">
                    <input type="text" autoComplete="off" onKeyUp={(e) => enterKey(e)} name="message" id="message" onChange={e => setMessage(e.target.value)} value={message} placeholder="Enter Your Message" />
                    <button id="message-send-button" onClick={() => sendMessage()}>Send</button>
                </div>
            </div>
        </div>
    )
}

export default Home;
